//  src/App.js
import { defineComponent, h, computed, ref } from '@vue/runtime-core'
import StartPage from './page/StartPage'
import GamePage from './page/GamePage'
import EndPage from './page/EndPage'

export default defineComponent({
  setup() {
    // curPageName = 'StartPage'  curPageName  computed
    //  ref 
    const curPageName = ref('StartPage')
    //  curPage 
    const curPage = computed(() => {
      switch (curPageName.value) {
        case 'StartPage':
          return StartPage
        case 'GamePage':
          return GamePage
        case 'EndPage':
          return EndPage
        default:
          return StartPage
      }
    })
    return {
      curPageName,
      curPage
    }
  },
  render(ctx) {
    //  h 
    const vnode = h("Container", [
      h(ctx.curPage, {
        onChangePage(page) {
          console.log('change to ' + page)
          ctx.curPageName = page
        }
      })
    ])
    // console.log(vnode)
    return vnode
  }
})